@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Table {
  width: 100%;

  & table {
    border-collapse: collapse;
    width: 100%;
  }

  & tr {
    height: 60px;
    border-bottom: thin solid $light-gray-color;

    &:nth-of-type(1) {
      border-top: thin solid $light-gray-color;
    }

    &:hover {
      background-color: #FAFAFA;

      & td:nth-of-type(1) {
        color: $red-color;
      }
    }
  }

  & td {
    padding-left: 50px;
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: 30px;
    margin-bottom: 10px;
    min-height: 100px;

    &:nth-of-type(1) {
      font-weight: 500;
    }

    @media (max-width: $medium-size) {
      font-weight: 300;
      font-size: 16px;
      line-height: 24px;
      padding-left: 5px;
    }
  }
}
